html, body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

#root {
  height: 100%;
  display: block;
  padding: 0;
  margin: 0;
}

.container-fluid {
  height: 100%;
  display: block;
  padding: 0;
  margin: 0;
}

.col-1 .col-2 .col-3 .col-4 .col-5 .col-6 .col-7 .col-8 .col-9 .col-10 .col-11 .col-12 {
  padding: 0;
}

.title {
  text-align: center;
}

.page {
  height: 100%;
  max-width: 100%;
  padding: 0;
  margin: 0;
  padding-top: 75px;
  display: block;

  @media (max-width: 400px) {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    padding-top: 18%;
  }
}

.under-title {
  text-align: center;
  color: rgb(48, 48, 48);
}

.error {
  color: red;
}

.top {
  margin-top: 25px;
}

.start-title {
  text-align: start;
}

.padding-10 {
  padding: 10px;
}

.btn-bg {
  width: 100%;
  margin-top: 10px;
}

@media (max-width: 400px) {
  .btn-bg {
    width: 100%;
  }
}

.custom-input {
  opacity: 0;
  z-index: 10;
}

.col .col-sm .col-sm-12 {
  padding-right: 0;
  padding-left: 0;
}